let filterMenus = (() => {
  let menus = []
  for (let key in allMenus) {
    if (agentMenu[currentAgent].indexOf(key) > -1) {
      menus.push(allMenus[key])
    }
  }
  if (menus.length <= 0) {
    return allMenus
  } else {
    return menus
  }
})()

export function getLeftMenus () {
  return filterMenus
}

