import axios from 'axios'
import qs from 'qs'
import { httpUrl } from '../http_url.js'

//axios
axios.default.timeout = 5000;
axios.default.headers = {'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8'};
axios.default.baseURL = httpUrl;


axios.interceptors.request.use(config => {    // config
    // localStorageapi_token
    if (localStorage.getItem('api_token')) {
        //  api_token request header
        config.headers.apiToken = `${localStorage.getItem('api_token')}`;
    }
    return config;
}, err => {
    return Promise.reject(err);
});


axios.interceptors.response.use(response => {
	return response
}, error => {
	return Promise.resolve(error.response)
});

function checkStatus(response) {
	// http
	if(response && (response.status === 200 || response.status === 304 ||
			response.status === 400)) {
		return response
	}
	// 
	return {
		status: -404,
		msg: ''
	}
}

function checkCode(res) {
	// code()
	if(res.status === -404) {
		alert(res.msg)
	}
	if(res.data && (!res.data.success)) {
		// alert(res.data.error_msg)
	}
	return res
}

// 
export default {
	post(url, data) { //  post
		return axios({
			method: 'post',
			baseURL: httpUrl,
			url,
			data: qs.stringify(data),
			timeout: 5000,
			headers: {
//				'X-Requested-With': 'XMLHttpRequest',
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
			}
		}).then(
			(response) => {
				return checkStatus(response)
			}
		).then(
			(res) => {
				return checkCode(res)
			}
		)
	},
	get(url, params) { // get
		return axios({
			method: 'get',
			baseURL: httpUrl,
			url,
			params, // get 
			timeout: 5000,
			headers: {
//				'X-Requested-With': 'XMLHttpRequest'
			}
		}).then(
			(response) => {
				return checkStatus(response)
			}
		).then(
			(res) => {
				return checkCode(res)
			}
		)
	}
}


