<template>
	<div class="report-game-list">
		<mu-appbar color="primary" class='start'>
			<mu-flex slot="left">
				<back></back>
			</mu-flex>
			

			<mu-button flat small slot="right" class="user-text" color="primary" @click="openSearchDialog">
				<mu-icon value="search" size="24"></mu-icon>
			</mu-button>
		</mu-appbar>

		<mu-container style="padding:0 16px;overflow: hidden;">
			<mu-load-more @refresh="refresh" :refreshing="refreshing" ref="container">
				<mu-form :model='name'>
					<mu-form-item class='choseType'>
						<span :style="{borderBottom:teamborder}" @click='chose'></span>
						<span :style="{borderBottom:agentborder}" @click='chose'></span>
					</mu-form-item>
				</mu-form>

				<span class='tip_text' ref='tip_text' :style='{color:tip_textColor,background:tip_textbgc}'>{{tip_text}}</span>

				<mu-paper :z-depth="3" v-for='item in datalist' :key='item.id'>
					<mu-list>
						<mu-list-item avatar class='infoTitle'>
							<mu-list-item-action class='infoTitle_msg'>{{item.created}}</mu-list-item-action>
							<mu-list-item-content></mu-list-item-content>
							<mu-list-item-action class='infoTitle_msg'>{{item.name}}</mu-list-item-action>
						</mu-list-item>
						<mu-list-item avatar>
							<mu-list-item-action>
								<span class="infoText"> {{item.id}}</span>
							</mu-list-item-action>
							<mu-list-item-content></mu-list-item-content>
							<mu-list-item-action>
								ID <span class="infoText">{{item.gameId}}</span>
							</mu-list-item-action>
						</mu-list-item>
						<mu-divider></mu-divider>
						<mu-list-item avatar>
							<mu-list-item-action>
								<span class="infoText">{{item.gname}}</span>
							</mu-list-item-action>
							<mu-list-item-content></mu-list-item-content>
							<mu-list-item-action>
								<span class="infoText">{{item.sname}}</span>
							</mu-list-item-action>
						</mu-list-item>
						<mu-divider></mu-divider>
						<mu-list-item avatar>
							<mu-list-item-action>
								{{dataText}}<span class="infoText" v-if="item.profit>0" style="color: green !important;">{{item.profit}}</span>
								<span class="infoText" v-else style="color: red !important;">{{item.profit}}</span>
							</mu-list-item-action>
							<mu-list-item-content></mu-list-item-content>
							<mu-list-item-action>
								{{dataText2}}<span class="infoText">{{item.tax}}</span>
							</mu-list-item-action>
						</mu-list-item>
					</mu-list>
				</mu-paper>

			</mu-load-more>

			<!--  -->
			<mu-dialog width="360" transition="slide-bottom" fullscreen :open.sync="openSearch">
				<mu-appbar color="primary" title="">
					<mu-button slot="left" icon @click="openSearch=false">
						<mu-icon value="keyboard_backspace"></mu-icon>
					</mu-button>
				</mu-appbar>
				<mu-container style="padding: 16px;overflow: hidden;">
					<mu-form :model="search" class="mu-demo-form" label-width="100">
						<mu-form-item>
							<mu-form-item class='search_title'></mu-form-item>
							<mu-divider></mu-divider>
						</mu-form-item>
						<mu-form-item label="">
							<mu-text-field v-model="search.agentName" placeholder=""></mu-text-field>
						</mu-form-item>
						<mu-form-item label="">
							<mu-row gutter align-items="center">
								<mu-col span="5">
									<mu-date-input v-model="search.startDate" help-text='' container="bottomSheet" :max-date="maxDate" :min-date="minDate" type='date'></mu-date-input>
								</mu-col>
								<mu-col span="2" style="color: #000;"></mu-col>
								<mu-col span="5">
									<mu-date-input v-model="search.endDate" help-text='' container="bottomSheet" :max-date="maxDate" :min-date="minDate" type='date'></mu-date-input>
								</mu-col>
							</mu-row>
						</mu-form-item>
					</mu-form>
					<mu-button large color="primary" class="search-btn" @click="closeSearchDialog"></mu-button>
				</mu-container>
			</mu-dialog>

		</mu-container>

	</div>
</template>

<script type="text/javascript">
	import back from 'base/back'
	import { prefix, currentAgent } from 'common/js/config.js'
	import { primaryColor } from 'common/js/config.js'
	import eventBus from 'common/js/event-bus.js'
	export default {
		data() {
			var maxDate = new Date();
			var minDate = new Date();
			minDate.setMonth(maxDate.getMonth() - 1);
			return {
				user: null,
				maxDate,
				minDate,
				name: {},
				types: 'recharge',
				teamborder: '#2196F3 2px  solid',
				agentborder: 'rgb(250,250,250) 2px  solid',
				pagenum: 1,
				search: {
					agentName: '',
					startDate: '',
					endDate: ''
				},
				datalist: {},
				gameId: '',
				refreshing: false,

				openSearch: false,
				tip_text: '*',
				tip_textColor: '#f44336',
				tip_textbgc: 'none'

			}
		},
		computed: {
			getPrimaryColor() {
				return `color:${primaryColor};`
			}
		},
		created() {
			this.user = this.$apps.spreadUser();
			if(this.types == 'recharge') {
				this.loadone();
			} else {
				this.loadtwo();
			}
			window.addEventListener('scroll', () => {
				this.woListScroll()
			}, false);

		},
		methods: {
			refresh() {
				// 
				this.refreshing = true;
				this.$refs.container.scrollTop = 0;
				setTimeout(() => {
					this.refreshing = false;
				}, 1200)
				this.loadone();
			},
			woListScroll() {
				var scroll = document.documentElement.scrollTop || document.body.scrollTop;
				if(scroll >= 100) {
					this.topUp = true;
				} else {
					window.clearInterval(this.temp);
					this.topUp = false;
				}

			},
			onTop() {
				var that = this;
				var num = setInterval(function() {
					document.documentElement.scrollTop = document.documentElement.scrollTop - 150 || 0;
				}, 50)
				that.temp = num;
				//				this.$el.childNodes[4].scrollTop= 0 ;
			},
			loadone() {
				eventBus.$emit('showNotification', '....');
				this.$axios.post('api/game//getPayList', {
						token: this.user.token,
						begDate: this.search.startDate,
						endDate: this.search.endDate,
						nickName: this.search.agentName,
						page: this.pagenum,
						gameId: this.gameId
					})
					.then((response) => {
						//						console.log(response.data.data)
						if(response.data && response.data.status == 0 && response.data.data) {
							if(!response.data.data || response.data.data == '') {
								eventBus.$emit('showNotification', '..')
								return;
							}
							this.datalist = {};
							this.dataText = '';
							this.dataText2 = '';
							this.datalist = response.data.data.dataLst;
							for(var i = 0; i < this.datalist.length; i++) {
								this.datalist[i].profit = Number(this.datalist[i].profit);
							}
						} else if(!response.data.status) {
							eventBus.$emit('showNotification', '')
						} else {
							eventBus.$emit('showNotification', response.data.msg)
						}
					})
					.catch(function(err) {
						eventBus.$emit('showNotification', err)
					})
			},
			loadtwo() {
				eventBus.$emit('showNotification', '....');
				this.$axios.post('api/game/getWithdrawList', {
						token: this.user.token,
						page: this.pagenum,

					})
					.then((response) => {
						//						console.log(response.data.data)
						if(response.data && response.data.status == 0 && response.data.data) {
							if(!response.data.data.withdrawLst || response.data.data.withdrawLst == '') {
								eventBus.$emit('showNotification', '..')
								return;
							}
							this.datalist = {};
							this.dataText = '';
							this.dataText2 = '';
							this.datalist = response.data.data.withdrawLst;
							for(var i = 0; i < this.datalist.length; i++) {
								this.datalist[i].profit = Number(this.datalist[i].profit);
							}
						} else if(!response.data.status) {
							eventBus.$emit('showNotification', '')
						} else {
							eventBus.$emit('showNotification', response.data.msg)
						}
					})
					.catch(function(err) {
						eventBus.$emit('showNotification', err)
					})
			},
			getImgUrl(hasData) {
				let url = `${prefix}${currentAgent}-icon/`
				if(hasData) {
					url += 'icon-item-have.png'
				} else {
					url += 'icon-item-none.png'
				}
				return url
			},
			chose(e) {
				var type = e.target.innerHTML;
				if(type == '') {
					this.teamborder = '#2196F3 2px  solid';
					this.agentborder = 'rgb(250,250,250) 2px  solid';
					this.types = 'recharge';
					this.loadone();
				} else {
					this.agentborder = '#2196F3 2px  solid';
					this.teamborder = 'rgb(250,250,250) 2px  solid';
					this.types = 'withdraw';
					this.loadtwo();
				}
			},

			openSearchDialog() {
				this.openSearch = true;

			},
			closeSearchDialog() {
				let d1 = this.search.startDate;
				let d2 = this.search.endDate;
				if(!d1 || !d2) {
					eventBus.$emit('showNotification', '!')
					return;
				}
				if(this.search.startDate && this.search.endDate && d1 > d2) {
					eventBus.$emit('showNotification', '!')
					return;
				}

				this.search.startDate = this.$formatDate(this.search.startDate); //string
				this.search.endDate = this.$formatDate(this.search.endDate);

				this.tip_text = ':' + this.search.startDate + '--' + this.search.endDate + '' + this.search.agentName;
				this.tip_textColor = 'rgb(51,54,63)';
				this.tip_textbgc = 'rgb(250,250,250)';
				this.loadone();

				this.search.agentName = ''
				this.search.startDate = ''
				this.search.endDate = ''
				this.openSearch = false
			},

		},
		components: {
			'back': back,
		},

	}
</script>

<style lang="scss" rel="stylesheet/sass" scoped>
	.list-item {
		margin-bottom: 10px;
		.item-image {
			position: relative;
			flex: 0 0 120px;
			line-height: 63px;
			height: 63px;
			color: #fff;
			img {
				display: block;
				width: 100%;
			}
			.text {
				position: absolute;
				top: 0;
				left: 5px;
			}
		}
		.list-content {
			.content-info {
				margin: 0 auto;
			}
		}
	}
	
	.choseType {
		width: 100% !important;
		padding: 0 !important;
		margin: 0 !important;
		line-height: 3;
		font-size: 18px;
		font-weight: 800;
		display: flex;
		flex-direction: row;
		justify-content: center;
		span {
			padding-left: 30px;
			padding-right: 30px;
		}
	}
	
	.search_title {
		font-size: 15px !important;
		color: rgb(33, 150, 243) !important;
	}
	
	.tip_text {
		padding-top: 5px !important;
		padding-left: 23% !important;
		width: max-content !important;
		margin: 0 auto !important;
		font-size: 12px !important;
	}
	
	.mu-form-item {
		margin-bottom: 5px;
	}
	
	.search-btn {
		width: 100%;
	}
	
	.helpInfo {
		position: fixed;
		right: 15px !important;
		left: 15px !important;
		z-index: 999 !important;
		background-color: rgb(33, 150, 243) !important;
		padding: 10px !important;
		font-size: 12px !important;
		overflow: inherit !important;
		white-space: normal !important;
	}
</style>


