<template>
	<div class="refresh">

	</div>
</template>

<script type="text/javascript">
	export default {
		data() {
			return {

			}
		},
		computed: {

		},
		created() {
			var that = this;
			var stats =that.$router.history.current.query.stats;
			//
			if(stats){
				setTimeout(function(){
					that.load2();
				},50)
				
			}else{
				this.load();
			}

		},
		methods: {
			load(){
				//  
				window.location.replace('/home/');				
				console.log('---')
			},
			load2(){
				//
				this.$router.go(-1);
			}
		},
		components: {

		}
	}
</script>

