<template>
	<div class="manage-card-edit">
		<mu-appbar color="primary">
			<mu-flex slot="left">
				<back></back>
			</mu-flex>
			
		</mu-appbar>
		<div class="block-title" :style="getPrimaryColor"></div>
		<div class="user-names">
			<mu-container>
				<mu-form ref="form" :model="validateForm" class="mu-demo-form" :label-position="labelPosition">
					<mu-form-item label="" prop="CardUser">
						<mu-text-field v-model="validateForm.CardUser" prop="CardUser" placeholder="16"></mu-text-field>
					</mu-form-item>
					<mu-form-item label="" prop="CardName">
						<mu-text-field v-model="validateForm.CardName" prop="CardName" placeholder="16-19"></mu-text-field>
					</mu-form-item>
				</mu-form>
			</mu-container>
			<div class="item-content">
				<div class="item-inner" @click="addCardList">
					<div class="item-title"></div>
					<div class="item-after">
						
						<mu-icon value="keyboard_arrow_right" class="keyboard_arrow_right"></mu-icon>
					</div>
				</div>
			</div>
			<mu-container class="user-name">
				<mu-form ref="form" :model="validateForm" class="mu-demo-form" :label-position="labelPosition">
					<mu-form-item label="" prop="CardUserPwd">
						<mu-text-field v-model="validateForm.CardUserPwd" prop="CardUserPwd" placeholder="" type="password"></mu-text-field>
					</mu-form-item>
					<mu-form-item label="" prop="CardNameNum">
						<mu-text-field v-model="validateForm.CardNameNum" prop="CardNameNum" placeholder="16-19"></mu-text-field>
					</mu-form-item>
				</mu-form>
			</mu-container>
		</div>

		<div class="item-margin">
			<mu-button color="primary" class="addCard-btns"></mu-button>
		</div>
	</div>
</template>

<script type="text/javascript">
	import back from 'base/back'
	import reportList from 'base/report-list'
	import { primaryColor } from 'common/js/config.js'
	import { getIconUrl } from 'common/js/tools.js'
	import { requestDomain, ERROR_OK } from 'api/config.js'
	import eventBus from 'common/js/event-bus.js'
	import { getAccountInfo, getBankList, bankCardWithdraw } from 'api/manage-transfer.js'
	export default {
		data() {
			return {
				user:null,
				labelPosition: 'top',
				validateForm: {
					email: '',
				},
				bank: '',
			}
		},
		created() {
			this.getIconUrl = getIconUrl
			this.user = this.$apps.spreadUser();
			eventBus.$on('change',(msg)=>{
				if(msg==bankLists){
					console.log('6666')
				}
			})
		},
		methods: {
			addCardList() {
				this.$router.push({
					path: '/manage/card/name'
				})
			},
			_get (msg) {
				this.banks=msg
			}
		},
		computed: {
			getPrimaryColor() {
				return `color:${primaryColor};`
			}
		},
		components: {
			'back': back,
		}
	}
</script>
<style lang="scss" scoped>
	.manage-card-edit {
		width: 100%;
		height: 100%;
		position: absolute;
		background-color: #fff;
	}
	
	div {
		margin: 0;
		padding: 0;
	}
	
	.block-title {
		margin: 32px 16px 16px;
		line-height: 16px;
		font-weight: 500;
	}
	
	.mu-text-field-input {
		height: 36px;
	}
	
	.user-names {
		padding-left:12px;
	}
	
	.mu-form-item {
		margin-bottom: 0;
		padding-bottom: 0;
	}
	
	.mu-form-item-label {
		font-size: 12px;
	}
	
	.item-inner {
		position: relative;
		width: 100%;
		padding-top: 8px;
		padding-bottom: 8px;
		padding-right: 10px;
		min-width: 0;
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: justify;
		-webkit-justify-content: space-between;
		-ms-flex-pack: justify;
		justify-content: space-between;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		-webkit-box-align: center;
		-webkit-align-items: center;
		-ms-flex-align: center;
		align-items: center;
		-webkit-align-self: stretch;
		-ms-flex-item-align: stretch;
		align-self: stretch;
	}
	
	.item-inner:after {
		content: '';
		position: absolute;
		background-color: rgba(0, 0, 0, 0.12);
		display: block;
		z-index: 15;
		top: auto;
		right: auto;
		bottom: 0;
		left: 0;
		height: 1px;
		width: 100%;
		-webkit-transform-origin: 50% 100%;
		transform-origin: 50% 100%;
	}
	
	.item-content {
		min-height: 48px;
		padding-left: 12px;
	}
	
	.item-after {
		color: #757575;
		font-size: 14px;
		padding-left: 8px;
		max-width: 70%;
		overflow: hidden;
		text-overflow: ellipsis;
		position: relative;
		display: block;
		white-space: nowrap;
		-webkit-flex-shrink: 0;
		-ms-flex-negative: 0;
		flex-shrink: 0;
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
		margin-left: auto;
		height: 32px;
		line-height: 32px;
	}
	
	.item-title {
		height: 32px;
		line-height: 32px;
		font-size: 16px;
	}
	
	.addCard-btns {
		width: 100%;
		min-height: 48px;
		outline: none;
		border: none;
		font-size: 18px;
		font-weight: 500;
	}
	
	.item-margin {
		margin: 0 8px;
	}
	
	.keyboard_arrow_right {
		vertical-align: middle;
		line-height: 32px;
		margin-left: 12px;
	}
</style>


